define('geodosi/router', ['exports', 'ember', 'geodosi/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('home');
    this.route('radio');
    this.route('nature');
    this.route('medical');
  });

  exports['default'] = Router;

});